import React from 'react';
import './JoinPage.css'; // Ensure you have a corresponding CSS file

const JoinPage = ({ language }) => {
  return (
    <div className="join-page">
      <h1>{language === 'EN' ? 'Join Our Team' : ''}</h1>
      <p>{language === 'EN' ? 'Thank you for showing interest in KinesioLog[Y]. We invite you to email us your resume, and we will contact you accordingly.' : 'KinesioLog[Y]'}</p>
      <p><a href="mailto:kinesiolog.y.recruitment@gmail.com">kinesiolog.y.recruitment@gmail.com</a></p>
    </div>
  );
};

export default JoinPage;
