// SecondPage.js

import React from 'react';
import './SecondPage.css';

const SecondPage = ({ language }) => {
  return (
    <div className="second-page">
      <div className="text-container">
        <p>
          {language === 'EN' ? 'Meet the minds behind KinesioLog[Y]  a dynamic trio of kinesiology enthusiasts from McGill University, Montreal, Canada.' : 'KinesioLog[Y] - '}
        </p>
      </div>
    </div>
  );
};

export default SecondPage;
